
.home {
    background-image: url('https://bhubaneswarlive.com/wp-content/uploads/2016/11/Savin-NY-Website-Background-Web.jpg');
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center
}
.main-pic {
    background-image: url('https://images.pexels.com/photos/34092/pexels-photo.jpg?w=1260&h=750&dpr=2&auto=compress&cs=tinysrgb');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
}
.jumbotron {
    font-family: 'Abril Fatface', cursive;
    color: white;
    text-shadow: 0px 0px 10px black;
}
.btn-default {
    width: 100%;
}
.navbar ul {
    list-style-type: none;
}
@media (max-width: 2000px) {
.navbar-header {
        float: none;
}
.navbar-toggle {
        display: block;
}
.navbar-collapse {
        border-top: 1px solid transparent;
        box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.1);
}
.navbar-collapse.collapse {
        display: none!important;
}
.navbar-collapse.collapse.in {
        display: block!important;
}
.navbar-nav {
        float: none!important;
        margin: 7.5px -15px;
}
.navbar-nav>li {
        float: none;
}
.navbar-nav>li>a {
        padding-top: 10px;
        padding-bottom: 10px;
}
.state {
        width: 50%
}
}



