
.notepad textarea {
    width: 100%;
}
.date-col{
    padding-left: 40px;
    border-bottom: 1px solid black;
    border-top: 1px solid black;
    background: rgba(0, 0, 0, 0.664);
    color:whitesmoke;
}
.time-col{
    border-right: 1px solid black;
    border-bottom: 1px solid black;
}
.remove {
    display: none;
}
.activities{
    margin-top:5px;
    margin-bottom: 5px;
}
.event {
    height: 130px;
}
.event button {
    margin-right: 20px;
}
.event:hover .remove {
    display: inline-block;
}



